* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  height: 100vh;
  font-family: Poppins, sans-serif;
}

.split-container {
  height: 100vh;
  display: flex;
}

.left-side {
  background-color: #fff;
  flex: 1;
  justify-content: center;
  align-items: center;
  display: flex;
}

.form-wrapper {
  background-color: #fff;
  border-radius: 10px;
  width: 100%;
  max-width: 400px;
  padding: 40px;
  box-shadow: 0 4px 20px #0000000d;
}

h2 {
  text-align: center;
  margin-bottom: 20px;
}

form {
  flex-direction: column;
  display: flex;
}

label {
  margin-top: 15px;
  font-size: 14px;
}

input {
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-top: 5px;
  padding: 10px;
  font-size: 14px;
}

.checkbox {
  margin: 15px 0;
  font-size: 12px;
}

button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #3a5b22;
  border: none;
  border-radius: 5px;
  margin-top: 10px;
  padding: 12px;
  font-weight: bold;
}

button[type="submit"]:hover {
  background-color: #5c853e;
}

.divider {
  text-align: center;
  color: #aaa;
  margin: 20px 0;
}

.social-login {
  gap: 10px;
  display: flex;
}

.social {
  cursor: pointer;
  border: 1px solid #ccc;
  border-radius: 5px;
  flex: 1;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding: 10px;
  font-weight: bold;
  display: flex;
}

.social.google {
  color: #fff;
  background-color: #4285f4;
}

.social.apple {
  color: #fff;
  background-color: #000;
}

.social.google:hover {
  background-color: #5791f0;
}

.social.apple:hover {
  background-color: #201e1e;
}

.icon {
  width: 20px;
  height: 20px;
}

.signin {
  text-align: center;
  margin-top: 15px;
}

.signin a {
  color: #3b82f6;
  text-decoration: none;
}

.right-side img {
  object-fit: contain;
  width: 100%;
  height: 100%;
}
